'use client';

import React, { useState, useMemo } from 'react';
import {
    Box,
    Typography,
    Button,
    Paper,
    Autocomplete,
    TextField,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Checkbox,
    Chip,
    Container
} from '@mui/material';
import { CheckBoxOutlineBlank, CheckBox } from '@mui/icons-material';

// --- Types ---
interface Mapping {
    id: number;
    district: string;
    ulb: string;
}

// --- Mock Data ---
const DISTRICTS = [
    'Bengaluru Urban',
    'Bengaluru Rural',
    'Mysuru',
    'Hassan',
    'Shivamogga',
    'Tumakuru',
    'Belagavi',
    'Hubballi-Dharwad'
];

const ALL_ULBS = [
    'BBMP', 'Mysore City Corp', 'Hassan CMC', 'Shivamogga City Corp',
    'Tumakuru Corp', 'Belagavi Corp', 'Hubballi-Dharwad Corp',
    'Nelamangala CMC', 'Hoskote CMC', 'Hunsur CMC', 'Arsikere CMC',
    'Bhadravathi CMC', 'Tiptur CMC', 'Gokak CMC'
];

const THEME_BLUE = '#002147'; // Dark Navy Blue
const THEME_RED = '#ff0000'; // Standard bright red from screenshot

const MapUlbToDistrict: React.FC = () => {
    // State
    const [selectedDistrict, setSelectedDistrict] = useState<string | null>(null);
    const [selectedUlbs, setSelectedUlbs] = useState<string[]>([]);
    const [mappings, setMappings] = useState<Mapping[]>([
        { id: 1, district: 'Hassan', ulb: 'Hassan' },
        { id: 2, district: 'Mysuru', ulb: 'Mysore City Corp' },
        { id: 3, district: 'Shivamogga', ulb: 'Shivamogga City Corp' }
    ]);

    // Validation/Filtering Logic
    const mappedUlbNames = useMemo(() => new Set(mappings.map(m => m.ulb)), [mappings]);

    const availableUlbs = useMemo(() => {
        return ALL_ULBS.filter(ulb => !mappedUlbNames.has(ulb) || selectedUlbs.includes(ulb));
    }, [mappedUlbNames, selectedUlbs]);

    const handleAdd = () => {
        if (!selectedDistrict || selectedUlbs.length === 0) {
            alert('Please select a District and at least one ULB.');
            return;
        }

        const newMappings: Mapping[] = selectedUlbs.map((ulb, index) => ({
            id: Date.now() + index,
            district: selectedDistrict,
            ulb: ulb
        }));

        setMappings(prev => [...prev, ...newMappings]);
        setSelectedDistrict(null);
        setSelectedUlbs([]);
    };

    const handleDelete = (id: number) => {
        if (window.confirm('Are you sure you want to delete this mapping?')) {
            setMappings(prev => prev.filter(m => m.id !== id));
        }
    };

    const icon = <CheckBoxOutlineBlank fontSize="small" />;
    const checkedIcon = <CheckBox fontSize="small" />;

    return (
        <Box sx={{ width: '100%', display: 'flex', justifyContent: 'center', p: 4, bgcolor: '#f1f5f9', minHeight: '100%' }}>
            {/* Single Main Card Container */}
            <Paper
                elevation={3}
                sx={{
                    width: '100%',
                    maxWidth: '1200px',
                    borderRadius: 2,
                    overflow: 'hidden',
                    bgcolor: 'white',
                    p: 4,
                    boxShadow: '0 4px 6px -1px rgba(0,0,0,0.1)'
                }}
            >
                {/* Header Blue Bar */}
                <Box
                    sx={{
                        bgcolor: THEME_BLUE,
                        py: 1.5,
                        borderRadius: 1,
                        mb: 4,
                        textAlign: 'center',
                        boxShadow: '0 2px 4px rgba(0,0,0,0.2)'
                    }}
                >
                    <Typography variant="h6" sx={{ color: 'white', fontWeight: 600 }}>
                        Map ULBs to District
                    </Typography>
                </Box>

                {/* Input Row */}
                <Box sx={{ display: 'flex', gap: 2, mb: 4, alignItems: 'center' }}>
                    <Autocomplete
                        options={DISTRICTS}
                        value={selectedDistrict}
                        onChange={(event, newValue) => setSelectedDistrict(newValue)}
                        sx={{ flex: 1 }}
                        renderInput={(params) => (
                            <TextField
                                {...params}
                                placeholder="Select District Name"
                                size="small"
                                sx={{
                                    '& .MuiOutlinedInput-root': { borderRadius: 1 }
                                }}
                            />
                        )}
                    />

                    <Autocomplete
                        multiple
                        options={availableUlbs}
                        disableCloseOnSelect
                        value={selectedUlbs}
                        onChange={(event, newValue) => setSelectedUlbs(newValue)}
                        getOptionLabel={(option) => option}
                        renderOption={(props, option, { selected }) => (
                            <li {...props}>
                                <Checkbox
                                    icon={icon}
                                    checkedIcon={checkedIcon}
                                    style={{ marginRight: 8 }}
                                    checked={selected}
                                />
                                {option}
                            </li>
                        )}
                        sx={{ flex: 1.5 }}
                        renderInput={(params) => (
                            <TextField
                                {...params}
                                placeholder="Select ULB Name (Multi Selection)"
                                size="small"
                                sx={{
                                    '& .MuiOutlinedInput-root': { borderRadius: 1 }
                                }}
                            />
                        )}
                        renderTags={(value, getTagProps) => {
                            const numTags = value.length;
                            const limit = 2;
                            return (
                                <>
                                    {value.slice(0, limit).map((option, index) => (
                                        <Chip label={option} size="small" {...getTagProps({ index })} />
                                    ))}
                                    {numTags > limit && <Chip label={`+${numTags - limit}`} size="small" />}
                                </>
                            );
                        }}
                    />

                    <Button
                        variant="contained"
                        onClick={handleAdd}
                        sx={{
                            bgcolor: THEME_BLUE,
                            textTransform: 'none',
                            fontWeight: 600,
                            px: 5,
                            borderRadius: 1,
                            minWidth: '100px',
                            '&:hover': { bgcolor: '#365a9e' }
                        }}
                    >
                        Add
                    </Button>
                </Box>

                {/* Separator Line */}
                <Box sx={{ borderBottom: '1px solid #e0e0e0', mb: 3 }} />

                {/* Table */}
                <TableContainer
                    component={Box}
                    sx={{
                        border: '1px solid #e2e8f0',
                        borderRadius: 1,
                        overflow: 'hidden'
                    }}
                >
                    <Table size="medium">
                        <TableHead>
                            <TableRow sx={{ bgcolor: THEME_BLUE }}>
                                <TableCell sx={{ color: 'white', fontWeight: 600, width: '10%', pl: 3 }}>Sl No</TableCell>
                                <TableCell sx={{ color: 'white', fontWeight: 600, width: '30%' }}>District Name</TableCell>
                                <TableCell sx={{ color: 'white', fontWeight: 600, width: '40%' }}>ULB Name</TableCell>
                                <TableCell sx={{ color: 'white', fontWeight: 600, textAlign: 'center' }}>Action</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {mappings.map((row, index) => (
                                <TableRow key={row.id} sx={{ '&:nth-of-type(even)': { bgcolor: '#f8fafc' } }}>
                                    <TableCell sx={{ pl: 3 }}>{index + 1}</TableCell>
                                    <TableCell>{row.district}</TableCell>
                                    <TableCell>{row.ulb}</TableCell>
                                    <TableCell sx={{ textAlign: 'center' }}>
                                        <Button
                                            variant="contained"
                                            size="small"
                                            onClick={() => handleDelete(row.id)}
                                            sx={{
                                                textTransform: 'none',
                                                minWidth: '80px',
                                                bgcolor: THEME_RED,
                                                borderRadius: 1,
                                                boxShadow: 'none',
                                                '&:hover': { bgcolor: '#cc0000', boxShadow: 'none' }
                                            }}
                                        >
                                            Delete
                                        </Button>
                                    </TableCell>
                                </TableRow>
                            ))}
                            {mappings.length === 0 && (
                                <TableRow>
                                    <TableCell colSpan={4} align="center" sx={{ py: 4, color: '#94a3b8' }}>
                                        No mappings found
                                    </TableCell>
                                </TableRow>
                            )}
                        </TableBody>
                    </Table>
                </TableContainer>

            </Paper>
        </Box>
    );
};

export default MapUlbToDistrict;
