'use client';

import React, { useState } from 'react';
import {
    Box,
    Typography,
    TextField,
    Button,
    Paper,
    Grid,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    MenuItem,
    IconButton,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Chip,
    Fade,
    Tooltip,
    Autocomplete,
    Container
} from '@mui/material';
import {
    Add,
    UploadFile,
    Edit,
    Close,
    Download
} from '@mui/icons-material';

// --- Types ---
interface Employee {
    id: string;
    name: string;
    dob: string;
    mobile: string;
    email: string;
    address: string;
    pinCode: string;
    recruitmentType: string;
    joiningDate: string;
    ulbName: string;
    wardName: string;
    ulbDistrict: string;
    sectionName: string;
    subSectionName: string;
    designation: string;
    exitDate?: string;
    reasonForExit?: string;
    status: 'Active' | 'Inactive';
}

// --- Mock Data ---
const MOCK_EMPLOYEES: Employee[] = [
    {
        id: 'EMP001',
        name: 'Ramesh Kumar',
        dob: '1985-05-15',
        mobile: '9876543210',
        email: 'ramesh.k@karnataka.gov.in',
        address: '123, MG Road, Bengaluru',
        pinCode: '560001',
        recruitmentType: 'Permanent',
        joiningDate: '2010-06-01',
        ulbName: 'BBMP',
        wardName: 'Ward 1',
        ulbDistrict: 'Bengaluru Urban',
        sectionName: 'Health',
        subSectionName: 'Sanitation',
        designation: 'Health Inspector',
        status: 'Active'
    }
];

const THEME_BLUE = '#002147'; // Dark Navy Blue

const UserManagement: React.FC = () => {
    // State
    const [view, setView] = useState<'list' | 'form'>('list');
    const [employees, setEmployees] = useState<Employee[]>(MOCK_EMPLOYEES);
    const [searchQuery, setSearchQuery] = useState('');
    const [isUploadOpen, setIsUploadOpen] = useState(false);

    // Form State
    const initialFormState: Employee = {
        id: '',
        name: '',
        dob: '',
        mobile: '',
        email: '',
        address: '',
        pinCode: '',
        recruitmentType: '',
        joiningDate: '',
        ulbName: '',
        wardName: '',
        ulbDistrict: '',
        sectionName: '',
        subSectionName: '',
        designation: '',
        exitDate: '',
        reasonForExit: '',
        status: 'Active'
    };
    const [formData, setFormData] = useState<Employee>(initialFormState);
    const [isEditable, setIsEditable] = useState(true);

    // Helpers
    const handleInputChange = (field: keyof Employee, value: any) => {
        setFormData(prev => ({ ...prev, [field]: value }));
    };

    const validateForm = (data: Employee) => {
        if (!data.name || !data.dob || !data.mobile || !data.ulbName) {
            alert('Please fill mandatory fields');
            return false;
        }
        return true;
    };

    const handleCreateNew = () => {
        setFormData(initialFormState);
        setIsEditable(true);
        setView('form');
    };

    const handleEdit = (emp: Employee) => {
        setFormData(emp);
        setIsEditable(true);
        setView('form');
    };

    const handleSave = () => {
        if (!validateForm(formData)) return;

        // Upsert logic
        const existingIndex = employees.findIndex(e => e.id === formData.id);
        if (existingIndex >= 0) {
            const updated = [...employees];
            updated[existingIndex] = formData;
            setEmployees(updated);
            alert('User details updated successfully');
        } else {
            const newId = formData.id || `EMP${Math.floor(Math.random() * 10000)}`;
            setEmployees([...employees, { ...formData, id: newId }]);
            alert(`User created successfully with ID: ${newId}`);
        }
        setView('list');
    };

    // --- Sub-Components ---
    const renderList = () => (
        <Fade in={true}>
            <Box>
                {/* Header */}
                <Box sx={{ mb: 4 }}>
                    <Typography variant="h4" sx={{ fontWeight: 800, color: '#1e293b' }}>
                        Employee / User Details
                    </Typography>
                    <Typography variant="body2" sx={{ color: '#64748b' }}>
                        Manage system users, create new employees, or bulk upload via Excel.
                    </Typography>
                </Box>

                {/* Toolbar */}
                <Paper sx={{ p: 3, mb: 4, borderRadius: 2, display: 'flex', gap: 2, alignItems: 'center', flexWrap: 'wrap' }}>
                    <Button
                        variant="contained"
                        startIcon={<Add />}
                        onClick={handleCreateNew}
                        sx={{ bgcolor: THEME_BLUE, fontWeight: 600, '&:hover': { bgcolor: '#001a38' }, textTransform: 'none' }}
                    >
                        Create New User
                    </Button>
                    <Button
                        variant="contained"
                        startIcon={<UploadFile />}
                        onClick={() => setIsUploadOpen(true)}
                        sx={{ bgcolor: THEME_BLUE, fontWeight: 600, '&:hover': { bgcolor: '#001a38' }, textTransform: 'none' }}
                    >
                        Add Users through Excel Sheet
                    </Button>

                    <Box sx={{ flexGrow: 1 }} />

                    <Box sx={{ display: 'flex', gap: 1, alignItems: 'center', width: 400 }}>
                        <Autocomplete
                            freeSolo
                            options={employees}
                            getOptionLabel={(option) => typeof option === 'string' ? option : `${option.name} (${option.id})`}
                            value={employees.find(e => e.name === searchQuery) || null}
                            onChange={(event, newValue) => {
                                if (newValue && typeof newValue !== 'string') setSearchQuery(newValue.name);
                                else if (typeof newValue === 'string') setSearchQuery(newValue);
                                else setSearchQuery('');
                            }}
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    placeholder="Search User / Employee"
                                    sx={{
                                        '& .MuiOutlinedInput-root': { borderRadius: '8px', bgcolor: 'white' }
                                    }}
                                    fullWidth
                                    size="small"
                                />
                            )}
                            sx={{ flexGrow: 1 }}
                        />
                        <Button variant="contained" sx={{ bgcolor: THEME_BLUE, borderRadius: '8px', textTransform: 'none', px: 4 }}>Search</Button>
                    </Box>
                </Paper>

                {/* Table */}
                <Paper sx={{ borderRadius: 2, overflow: 'hidden', boxShadow: '0 4px 6px -1px rgba(0,0,0,0.1)' }}>
                    <Box sx={{ bgcolor: THEME_BLUE, p: 2, color: 'white' }}>
                        <Typography variant="subtitle1" fontWeight={600}>Employee List</Typography>
                    </Box>
                    <TableContainer>
                        <Table>
                            <TableHead sx={{ bgcolor: '#f8fafc' }}>
                                <TableRow>
                                    {['ID', 'Name', 'Designation', 'ULB', 'Mobile', 'Status', 'Actions'].map(head => (
                                        <TableCell key={head} sx={{ fontWeight: 700, color: '#475569' }}>{head}</TableCell>
                                    ))}
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {employees.filter(e =>
                                    e.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
                                    e.id.toLowerCase().includes(searchQuery.toLowerCase())
                                ).map((emp) => (
                                    <TableRow key={emp.id} hover>
                                        <TableCell>{emp.id}</TableCell>
                                        <TableCell>
                                            <Box>
                                                <Typography variant="subtitle2" fontWeight={600} color="#1e293b">{emp.name}</Typography>
                                                <Typography variant="caption" color="#64748b">{emp.email}</Typography>
                                            </Box>
                                        </TableCell>
                                        <TableCell>{emp.designation}</TableCell>
                                        <TableCell>{emp.ulbName}</TableCell>
                                        <TableCell>{emp.mobile}</TableCell>
                                        <TableCell>
                                            <Chip
                                                label={emp.status}
                                                size="small"
                                                color={emp.status === 'Active' ? 'success' : 'default'}
                                                variant="outlined"
                                            />
                                        </TableCell>
                                        <TableCell>
                                            <Tooltip title="Edit Details">
                                                <IconButton size="small" color="primary" onClick={() => handleEdit(emp)}>
                                                    <Edit fontSize="small" />
                                                </IconButton>
                                            </Tooltip>
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Paper>
            </Box>
        </Fade>
    );

    const renderForm = () => {
        const fieldStyle = {
            '& .MuiOutlinedInput-root': { bgcolor: 'white', borderRadius: 1, height: '40px' },
            '& .MuiInputBase-input': { py: 1, fontSize: '14px' }
        };

        const renderFieldRow = (label: string, field: keyof Employee, type: string = 'text', options: string[] = []) => (
            <Grid container key={field} alignItems="center" spacing={1} sx={{ mb: 2 }}>
                <Grid item xs={4} sx={{ textAlign: 'right', pr: 2 }}>
                    <Typography variant="body2" sx={{ fontWeight: 600, color: '#333', whiteSpace: 'pre-line' }}>{label}</Typography>
                </Grid>
                <Grid item xs={8}>
                    {type === 'select' ? (
                        <TextField
                            select fullWidth size="small"
                            value={(formData as any)[field] || ''}
                            onChange={(e) => handleInputChange(field, e.target.value)}
                            disabled={!isEditable}
                            sx={fieldStyle}
                        >
                            {options.map(opt => <MenuItem key={opt} value={opt}>{opt}</MenuItem>)}
                        </TextField>
                    ) : (
                        <TextField
                            fullWidth size="small" type={type}
                            value={(formData as any)[field] || ''}
                            onChange={(e) => handleInputChange(field, e.target.value)}
                            disabled={!isEditable}
                            sx={fieldStyle}
                            InputLabelProps={type === 'date' ? { shrink: true } : {}}
                        />
                    )}
                </Grid>
            </Grid>
        );

        return (
            <Box sx={{ bgcolor: 'white', minHeight: '100%', border: '1px solid #ccc', pb: 4 }}>
                <Box sx={{ bgcolor: THEME_BLUE, p: 1, px: 2, display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                    <Typography variant="h6" sx={{ color: 'white', fontWeight: 500, fontSize: '1rem' }}>Employee /User Details</Typography>
                    <IconButton size="small" sx={{ color: 'white' }} onClick={() => setView('list')}><Close /></IconButton>
                </Box>

                <Container maxWidth="lg" sx={{ py: 4 }}>
                    <Grid container spacing={4}>
                        {/* Column 1 */}
                        <Grid item xs={12} md={4}>
                            {renderFieldRow("User Name", 'name')}
                            {renderFieldRow("Email Id", 'email', 'email')}
                            {renderFieldRow("Recruitment\nType", 'recruitmentType', 'select', ['Permanent', 'Contract', 'Temporary'])}
                            {renderFieldRow("Ward Name", 'wardName', 'select', ['Ward 1', 'Ward 2', 'Ward 3'])}
                            {renderFieldRow("Employee\nID/Code", 'id')}
                            {renderFieldRow("Exit Date", 'exitDate', 'date')}
                        </Grid>

                        {/* Column 2 */}
                        <Grid item xs={12} md={4}>
                            {renderFieldRow("Date of Birth", 'dob', 'date')}
                            {renderFieldRow("Address", 'address')}
                            {renderFieldRow("Joining Date", 'joiningDate', 'date')}
                            {renderFieldRow("Section Name", 'sectionName', 'select', ['Health', 'Revenue', 'Admin'])}
                            {/* Empty space to align if needed, or Reason for Exit higher up */}
                            {renderFieldRow("Reason for Exit", 'reasonForExit')}
                        </Grid>

                        {/* Column 3 */}
                        <Grid item xs={12} md={4}>
                            {renderFieldRow("Mobile Number", 'mobile')}
                            {renderFieldRow("Pin Code", 'pinCode')}
                            {renderFieldRow("ULB Name", 'ulbName', 'select', ['BBMP', 'Mysore City Corp'])}
                            {renderFieldRow("Designation", 'designation', 'select', ['Inspector', 'Manager', 'Clerk'])}
                            {renderFieldRow("Status", 'status', 'select', ['Active', 'Inactive'])}
                        </Grid>
                    </Grid>

                    <Box sx={{ display: 'flex', justifyContent: 'center', mt: 6 }}>
                        {/* Only "Save" button in Green as per screenshot */}
                        <Button
                            variant="contained"
                            sx={{
                                bgcolor: '#00b050',
                                color: 'white',
                                fontWeight: 'bold',
                                px: 6,
                                py: 1,
                                fontSize: '1rem',
                                '&:hover': { bgcolor: '#009040' },
                                textTransform: 'none',
                                borderRadius: 1
                            }}
                            onClick={handleSave}
                        >
                            Save
                        </Button>
                    </Box>
                </Container>
            </Box>
        );
    };

    return (
        <Box sx={{ p: isEditable && view === 'form' ? 0 : 4, bgcolor: '#f1f5f9', minHeight: '100%' }}>
            {view === 'list' ? renderList() : renderForm()}

            <Dialog open={isUploadOpen} onClose={() => setIsUploadOpen(false)} maxWidth="sm" fullWidth>
                <DialogTitle sx={{ bgcolor: THEME_BLUE, color: 'white', display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    <Typography variant="h6" fontWeight={600}>Upload Users via Excel</Typography>
                    <IconButton size="small" onClick={() => setIsUploadOpen(false)} sx={{ color: 'white' }}>
                        <Close />
                    </IconButton>
                </DialogTitle>
                <DialogContent dividers sx={{ p: 4 }}>
                    <Box sx={{ textAlign: 'center' }}>
                        <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
                            Download the template to ensure your data is formatted correctly before uploading.
                        </Typography>

                        <Button
                            variant="outlined"
                            startIcon={<Download />}
                            sx={{ mb: 4, textTransform: 'none', borderRadius: 2, borderColor: THEME_BLUE, color: THEME_BLUE }}
                        >
                            Download Excel Template
                        </Button>

                        <Box
                            sx={{
                                border: `2px dashed ${THEME_BLUE}`,
                                borderRadius: 3,
                                p: 6,
                                bgcolor: '#f8fafc',
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center',
                                gap: 2,
                                cursor: 'pointer',
                                transition: 'all 0.2s',
                                '&:hover': { bgcolor: '#f1f5f9', borderColor: '#001a38' }
                            }}
                        >
                            <UploadFile sx={{ fontSize: 48, color: THEME_BLUE, opacity: 0.7 }} />
                            <Box>
                                <Typography variant="subtitle1" fontWeight={600} color="#1e293b">Click or Drag file to upload</Typography>
                                <Typography variant="caption" color="#64748b">Support for .xlsx, .xls files</Typography>
                            </Box>
                        </Box>
                    </Box>
                </DialogContent>
                <DialogActions sx={{ p: 3, bgcolor: '#f8fafc' }}>
                    <Button onClick={() => setIsUploadOpen(false)} sx={{ color: '#64748b', fontWeight: 600 }}>Cancel</Button>
                    <Button
                        variant="contained"
                        onClick={() => setIsUploadOpen(false)}
                        sx={{ bgcolor: THEME_BLUE, fontWeight: 600, px: 4, '&:hover': { bgcolor: '#001a38' } }}
                    >
                        Upload Data
                    </Button>
                </DialogActions>
            </Dialog>
        </Box>
    );
};

export default UserManagement;
