'use client';

import React, { useState } from 'react';
import {
    Box,
    Typography,
    Paper,
    Button,
    Autocomplete,
    TextField,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Fade,
    Snackbar,
    Alert,
    Grid
} from '@mui/material';
import { Add } from '@mui/icons-material';

// --- Types ---
interface MappingRow {
    id: number;
    ulbType: string;
    section: string;
    role: string;
    level: string;
}

// --- Mock Data ---
const MOCK_ULB_TYPES = ['City Corporation', 'CMC', 'TMC', 'TP'];
const MOCK_SECTIONS = ['Engineering', 'Health', 'Revenue', 'Administration', 'Town Planning'];
const MOCK_ROLES = ['JE', 'AE', 'AEE', 'EE', 'DC', 'Commissioner', 'Health Inspector', 'Revenue Inspector', 'Chief Officer'];
const MOCK_LEVELS = ['Level 0', 'Level 1', 'Level 2', 'Level 3', 'Level 4', 'Level 5'];

const THEME_BLUE = '#002147';
const THEME_RED = '#ff0000';

const MapLevelsToRoles: React.FC = () => {
    // Form State
    const [ulbType, setUlbType] = useState<string | null>(null);
    const [section, setSection] = useState<string | null>(null);
    const [role, setRole] = useState<string | null>(null);
    const [level, setLevel] = useState<string | null>(null);

    // Data State
    const [mappings, setMappings] = useState<MappingRow[]>([
        { id: 1, ulbType: 'CMC', section: 'Engineering', role: 'JE', level: 'Level 5' },
        { id: 2, ulbType: 'CMC', section: 'Engineering', role: 'AE', level: 'Level 4' },
        { id: 3, ulbType: 'CMC', section: 'Engineering', role: 'AEE', level: 'Level 3' },
        { id: 4, ulbType: 'CMC', section: 'Engineering', role: 'EE', level: 'Level 2' },
        { id: 5, ulbType: 'CMC', section: 'Engineering', role: 'DC', level: 'Level 1' },
        { id: 6, ulbType: 'CMC', section: 'Engineering', role: 'Commissioner', level: 'Level 0' },
    ]);

    const [notification, setNotification] = useState<{ open: boolean; message: string; type: 'success' | 'error' | 'info' }>({
        open: false,
        message: '',
        type: 'success'
    });

    // Handlers
    const handleAdd = () => {
        if (!ulbType || !section || !role || !level) {
            setNotification({ open: true, message: 'Please select all fields', type: 'error' });
            return;
        }

        // Validation: System should not allow user to add the same Role to add different Levels in Same section.
        // Check if there is already an entry for this (ULB Type + Section + Role)
        const conflict = mappings.some(
            m => m.ulbType === ulbType && m.section === section && m.role === role
        );

        if (conflict) {
            setNotification({
                open: true,
                message: `The Role '${role}' is already mapped in '${section}' for '${ulbType}'. Cannot add different Level.`,
                type: 'error'
            });
            return;
        }

        const newMapping: MappingRow = {
            id: Date.now(),
            ulbType,
            section,
            role,
            level
        };

        setMappings([...mappings, newMapping]);
        setNotification({ open: true, message: 'Role mapped successfully', type: 'success' });
    };

    const handleDelete = (id: number) => {
        setMappings(mappings.filter(m => m.id !== id));
        setNotification({ open: true, message: 'Mapping removed successfully', type: 'info' });
    };

    return (
        <Fade in={true}>
            <Box sx={{ maxWidth: '100%', mx: 'auto' }}>
                <Paper elevation={3} sx={{ borderRadius: 2, overflow: 'hidden' }}>
                    {/* Header */}
                    <Box sx={{ bgcolor: THEME_BLUE, py: 2, px: 3 }}>
                        <Typography variant="h6" sx={{ color: 'white', fontWeight: 600, textAlign: 'center' }}>
                            Map Levels to Roles
                        </Typography>
                    </Box>

                    {/* Controls Row */}
                    <Box sx={{ p: 4, bgcolor: '#fff' }}>
                        <Grid container spacing={2} alignItems="center">
                            <Grid item xs={12} md={2.75}>
                                <Autocomplete
                                    fullWidth
                                    options={MOCK_ULB_TYPES}
                                    value={ulbType}
                                    onChange={(_, newValue) => setUlbType(newValue)}
                                    renderInput={(params) => (
                                        <TextField {...params} label="ULB Type" variant="outlined" size="small" />
                                    )}
                                />
                            </Grid>
                            <Grid item xs={12} md={2.75}>
                                <Autocomplete
                                    fullWidth
                                    options={MOCK_SECTIONS}
                                    value={section}
                                    onChange={(_, newValue) => setSection(newValue)}
                                    renderInput={(params) => (
                                        <TextField {...params} label="Section" variant="outlined" size="small" />
                                    )}
                                />
                            </Grid>
                            <Grid item xs={12} md={2.75}>
                                <Autocomplete
                                    fullWidth
                                    options={MOCK_ROLES}
                                    value={role}
                                    onChange={(_, newValue) => setRole(newValue)}
                                    renderInput={(params) => (
                                        <TextField {...params} label="Role" variant="outlined" size="small" />
                                    )}
                                />
                            </Grid>
                            <Grid item xs={12} md={2.75}>
                                <Autocomplete
                                    fullWidth
                                    options={MOCK_LEVELS}
                                    value={level}
                                    onChange={(_, newValue) => setLevel(newValue)}
                                    renderInput={(params) => (
                                        <TextField {...params} label="Level" variant="outlined" size="small" />
                                    )}
                                />
                            </Grid>
                            <Grid item xs={12} md={1}>
                                <Button
                                    variant="contained"
                                    fullWidth
                                    onClick={handleAdd}
                                    sx={{
                                        bgcolor: THEME_BLUE,
                                        height: '40px',
                                        textTransform: 'none',
                                        fontWeight: 600,
                                        '&:hover': { bgcolor: '#001a38' }
                                    }}
                                >
                                    Add
                                </Button>
                            </Grid>
                        </Grid>
                    </Box>

                    {/* Divider/Spacing - Matches screenshot separator line */}
                    <Box sx={{ height: 2, bgcolor: '#e2e8f0', mx: 4 }} />

                    {/* Table */}
                    <Box sx={{ p: 4 }}>
                        <TableContainer sx={{ border: '1px solid #e2e8f0', borderRadius: 0 }}>
                            <Table size="medium">
                                <TableHead>
                                    <TableRow sx={{ bgcolor: THEME_BLUE }}>
                                        <TableCell sx={{ color: 'white', fontWeight: 700, width: '80px', textAlign: 'center', borderRight: '1px solid rgba(255,255,255,0.2)' }}>Sl No</TableCell>
                                        <TableCell sx={{ color: 'white', fontWeight: 700, borderRight: '1px solid rgba(255,255,255,0.2)', textAlign: 'center' }}>ULB Type</TableCell>
                                        <TableCell sx={{ color: 'white', fontWeight: 700, borderRight: '1px solid rgba(255,255,255,0.2)', textAlign: 'center' }}>Section</TableCell>
                                        <TableCell sx={{ color: 'white', fontWeight: 700, borderRight: '1px solid rgba(255,255,255,0.2)', textAlign: 'center' }}>Roles</TableCell>
                                        <TableCell sx={{ color: 'white', fontWeight: 700, borderRight: '1px solid rgba(255,255,255,0.2)', textAlign: 'center' }}>Level</TableCell>
                                        <TableCell sx={{ color: 'white', fontWeight: 700, textAlign: 'center', width: '120px' }}>Action</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {mappings.length === 0 ? (
                                        <TableRow>
                                            <TableCell colSpan={6} align="center" sx={{ py: 6, color: '#64748b' }}>
                                                No mappings found. Add one above.
                                            </TableCell>
                                        </TableRow>
                                    ) : (
                                        mappings.map((row, index) => (
                                            <TableRow key={row.id} sx={{ '&:nth-of-type(odd)': { bgcolor: '#e9effc' }, '&:hover': { bgcolor: '#f1f5f9' } }}>
                                                <TableCell align="center" sx={{ fontWeight: 500, color: '#000', borderRight: '1px solid #e2e8f0' }}>{index + 1}</TableCell>
                                                <TableCell align="center" sx={{ color: '#000', borderRight: '1px solid #e2e8f0' }}>{row.ulbType}</TableCell>
                                                <TableCell align="center" sx={{ color: '#000', borderRight: '1px solid #e2e8f0' }}>{row.section}</TableCell>
                                                <TableCell align="center" sx={{ color: '#000', borderRight: '1px solid #e2e8f0' }}>{row.role}</TableCell>
                                                <TableCell align="center" sx={{ color: '#000', borderRight: '1px solid #e2e8f0' }}>{row.level}</TableCell>
                                                <TableCell align="center">
                                                    <Button
                                                        variant="contained"
                                                        size="small"
                                                        onClick={() => handleDelete(row.id)}
                                                        sx={{
                                                            bgcolor: THEME_RED,
                                                            color: 'white',
                                                            textTransform: 'none',
                                                            fontWeight: 600,
                                                            px: 2,
                                                            boxShadow: 'none',
                                                            '&:hover': { bgcolor: '#d32f2f' }
                                                        }}
                                                    >
                                                        Delete
                                                    </Button>
                                                </TableCell>
                                            </TableRow>
                                        ))
                                    )}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Box>
                </Paper>

                {/* Notification */}
                <Snackbar
                    open={notification.open}
                    autoHideDuration={4000}
                    onClose={() => setNotification({ ...notification, open: false })}
                    anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                >
                    <Alert severity={notification.type} onClose={() => setNotification({ ...notification, open: false })} variant="filled">
                        {notification.message}
                    </Alert>
                </Snackbar>
            </Box>
        </Fade>
    );
};

export default MapLevelsToRoles;
